import React, { Component } from 'react';
import { InputNumber } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class XinputNumber extends Component {
	state = {
		value: 0
	}

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}
	
	change = (n) => {
		console.log(n)
	}

	render() {
		const { optionsjs } = this.props.configs
		return (
			<div id="x-input-number">
				<h3>Input Number 数字输入框</h3>
				<div className="mt-20 mb-20">
					<InputNumber defaultValue={this.state.value} onChange={this.change} min="0" max="10" step="2"/>
				</div>
				<Codemirror value={
					`<InputNumber defaultValue={this.state.value} onChange={this.change} min="0" max="10" step="2"/>`
				} options={optionsjs} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

	})(XinputNumber)
